<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        HCBP
      </div>
      <v-spacer></v-spacer>
      <v-btn color="primary" @click="data.dialog = true" depressed elevation="2">
        <v-icon>mdi-upload</v-icon>
        Upload Data HCBP</v-btn>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn depressed elevation="2" color="primary" @click="hapus(item.id)">
            <v-icon small class="mr-2">
              mdi-delete
            </v-icon> Delete
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Upload/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import Upload from '@/components/DataHCBP/Upload.vue'
import store from '@/store'
export default {
  name: 'OrganizationPage',
  components: {
    Upload
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: 'Nik',
        align: 'start',
        sortable: false,
        value: 'nik'
      },
      { text: 'Work Location', value: 'worklocation' },
      { text: 'Regional', value: 'regional' },
      { text: 'Area', value: 'area' },
      { text: 'Action', value: 'action' }
    ]
  }),
  mounted: function () {
    store.dispatch('datahcbp/reqData')
  },
  computed: {
    ...mapState('datahcbp', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('datahcbp', [
      'hapus'
    ])
  }
}
</script>
